<div class="main"><the-title-and-search class="the-search" :title="[&quot;班级列表&quot;]"><template #button><div class="btn-group flex flex-row flex-start"><a-button class="btn-i flex flex-row justify-between align-items-center" type="primary" @click="changeBoolean('modalClassAdd',true)"><b-icon type="iconjia"></b-icon><span class="pad-left-8">新建</span></a-button><a-button class="btn-i flex flex-row justify-between align-items-center btn-i-white" type="primary"><b-icon type="iconxiazai"></b-icon><a class="pad-left-8" href="./static/students.xls">下载学生模板</a></a-button><a-button class="btn-i flex flex-row justify-between align-items-center" type="primary" ghost @click="delClass(undefined)"><b-icon type="iconshanchu"></b-icon><span class="pad-left-8">删除</span></a-button></div></template><template #search><the-search @getSearch="searchChange"></the-search></template></the-title-and-search><a-table class="table" rowKey="id" size="middle" :columns="columnClass" :data-source="tableClassList" :pagination="false" :loading="loadingClassList" :row-selection="{ selectedRowKeys: selectedRowKeys, onChange: onSelectChange }" @change="tableSort"><template v-slot:list="{ text, record }"><div class="operation flex flex-row justify-between align-items-center"><div class="btn-link pointer btns-link-primary" @click="getStudentList(record)">查看</div></div></template><template v-slot:operation="{ text, record }"><div class="operation flex flex-row justify-between align-items-center"><div class="btn-link pointer btns-link-primary" @click="editClass(record)">编辑</div><div class="btn-column"></div><a-upload name="file" :multiple="true" :showUploadList="false" @change="fileChange"><div class="btn-link pointer btns-link-primary" @click="editClass(record, false)">导入</div></a-upload><div class="btn-column"></div><div class="btn-link pointer btns-link-primary" @click="delClass(record.id)">删除</div></div></template></a-table><div class="page flex flex-row justify-end"><a-pagination :size="pageConfig.size" :current="pageConfig.current" :total="pageConfig.totalSize" :page-size="pageConfig.pageSize" :page-size-options="pageConfig.options" :show-quick-jumper="pageConfig.showJumper" :showSizeChanger="pageConfig.showSize" @change="getFetchData" @showSizeChange="onChangeSize"></a-pagination></div><the-add key="modalClassAdd" :isEdit="false" :visible="modalVisibleAdd" @onSubmit="modalCallback('modalClassAdd')" @onCancel="modalCallback('modalClassAdd')"></the-add><the-edit key="modalClassEdit" :isEdit="true" :visible="modalVisibleEdit" @onSubmit="modalCallback('modalClassEdit')" @onCancel="modalCallback('modalClassEdit')"></the-edit><the-list key="modalStudentList" :visible="modalVisibleList" @onCancel="modalCallback('modalStudentList')"></the-list></div>