<div class="main"><the-title-and-search class="the-search" :title="tabTitle" @tabChange="tabChange"><template #button><div class="btn-group flex flex-row flex-start"><a-button class="btn-i flex flex-row justify-between align-items-center" type="primary" @click="changeBoolean(activeTab === 0?'modalTeacherAdd':'modalStudentAdd',true)"><b-icon type="iconjia"></b-icon><span class="pad-left-8">新建</span></a-button><a-button class="btn-i flex flex-row justify-between align-items-center" type="primary" ghost @click="delUser(undefined)"><b-icon type="iconshanchu"></b-icon><span class="pad-left-8">删除</span></a-button></div></template><template #search><the-search @getSearch="searchChange"></the-search></template></the-title-and-search><div class="table-container"><a-table class="table" rowKey="code" size="middle" :columns="activeTab === 0 ? columnTeacher : columnStudent" :data-source="activeTab === 0 ? teacherData : studentData" :pagination="false" :loading="loadingUserList" :row-selection="{ selectedRowKeys: selectedRowKeys, onChange: onSelectChange }"><template v-slot:operation="{ text, record }"><div class="operation flex flex-row justify-between align-items-center"><div class="btn-link pointer btns-link-primary" @click="editUser(record)">编辑</div><div class="btn-column"></div><div class="btn-link pointer btns-link-primary" @click="resetUser(record)">重置密码</div><div class="btn-column"></div><div class="btn-link pointer btns-link-primary" @click="delUser(record.code)">删除</div></div></template></a-table><div class="page flex flex-row justify-end"><a-pagination :size="pageConfig.size" :current="pageConfig.current" :total="pageConfig.totalSize" :page-size="pageConfig.pageSize" :page-size-options="pageConfig.options" :show-quick-jumper="pageConfig.showJumper" :showSizeChanger="pageConfig.showSize" @change="getFetchData" @showSizeChange="onChangeSize"></a-pagination></div></div><the-edit-student key="modalStudentEdit" :isEdit="true" :visible="modalEditStudent" @onSubmit="modalCallback('modalStudentEdit')" @onCancel="modalCallback('modalStudentEdit')"></the-edit-student><the-add-student key="modalStudentAdd" :isEdit="false" :visible="modalAddStudent" @onSubmit="modalCallback('modalStudentAdd')" @onCancel="modalCallback('modalStudentAdd')"></the-add-student><the-edit-teacher key="modalTeacherEdit" :isEdit="true" :visible="modalEditTeacher" @onSubmit="modalCallback('modalTeacherEdit')" @onCancel="modalCallback('modalTeacherEdit')"></the-edit-teacher><the-add-teacher key="modalTeacherAdd" :isEdit="false" :visible="modalAddTeacher" @onSubmit="modalCallback('modalTeacherAdd')" @onCancel="modalCallback('modalTeacherAdd')"></the-add-teacher></div>